const https = require('https');
const dl = require('./download-string');

const URL_BASE = "https://lawly.cloudant.com/bwb-ng/";

const fetchHtml = function (bwbId, expressionId) {
    const url = URL_BASE + bwbId + '/' + expressionId;
    return dl(url);
};

module.exports = {
    fetchHtml: fetchHtml
};