const React = require('react');
const _ = require('underscore');
const ReactDOM = require('react-dom');
const PureRenderMixin = require('react-addons-pure-render-mixin');

const ScreenHelper = require('../screen-helper');
const ReaderActions = require('./reader-actions');

const Entry = React.createClass({
    mixins: [PureRenderMixin],

    propTypes: {
        id: React.PropTypes.string.isRequired,
        label: React.PropTypes.string,
        sublabel: React.PropTypes.string,
        hasChildren: React.PropTypes.bool.isRequired
    },

    _onNavigateToItem: function (e) {
        //console.log("item clicked");
        if (ScreenHelper.isNarrow()) {
            ReaderActions.close();
        }
    },

    _toggleOpen: function (e) {
        e.preventDefault();
        this.setState({
            isOpen: !this.state.isOpen
        });
    },

    componentDidUpdate: function (prevProps, prevState) {
        console.log("ToC entry updated");
        console.log(prevProps)
        console.log(this.props)
        console.log(prevState)
        console.log(this.state)
    },

    getDefaultProps: function () {
        return {
            label: null,
            sublabel: null,
            rangeStart: null,
            rangeEnd: null
        }
    },

    getInitialState: function () {
        return {
            isOpen: false
        };
    },

    render: function () {
        //console.log("render toc-entry")

        var hasChildren = this.props.hasChildren;
        var label = this.props.label ? <div className="label">{this.props.label}</div> : '';
        var sublabel =  '';//this.props.sublabel ? <div className="sublabel">{this.props.sublabel}</div> : '';

        var range = '';
        //if (this.props.rangeStart) {
        //    if (this.props.rangeEnd) {
        //        range = <div className="range">{this.props.rangeStart+" — "+this.props.rangeEnd}</div>;
        //    } else {
        //        range = <div className="range">{this.props.rangeStart}</div>;
        //    }
        //}

        var className = "title-container labelling ";


        if (hasChildren) {
            className = className + ' sublist-labelling';
            var topClass = 'sublist' + (this.state.isOpen ? '' : ' collapsed');
            return <li className={topClass}>
                <div className={className}>
                    <a onClick={this._toggleOpen} href={'#'+this.props.id}>
                        <div className="title-container">
                            {label}
                            {sublabel}
                            {range}
                        </div>
                    </a>
                </div>
                {this.props.children}
            </li>
        } else {
            return <li className='item'>
                <div className={className}>
                    <a onClick={this._onNavigateToItem} href={'#'+this.props.id}>
                        <div className="title-container">
                            {label}
                            {sublabel}
                        </div>
                    </a>
                </div>
            </li>
        }
    }
});

module.exports = Entry;