var React = require('react');
var PureRenderMixin = require('react-addons-pure-render-mixin');

var QuickMenuItem = React.createClass({
    mixins: [PureRenderMixin],

    propTypes: {
        bwbId: React.PropTypes.string.isRequired,
        title: React.PropTypes.string.isRequired
    },

    getDefaultProps: function () {
        return {}
    },

    render: function () {
        return (
            <li className='doc-item'>
                <a href={"reader/?bwbId="+this.props.bwbId}>
                    <div className='source-hint'></div>
                    <div className='doc-info'>
                        {this.props.title}
                    </div>
                </a>
            </li>
        );
    }
});

module.exports = QuickMenuItem;