const React = require('react');
const _ = require('underscore');
const ReactDOM = require('react-dom');
const PureRenderMixin = require('react-addons-pure-render-mixin');

const Flag = require('./flag-background.jsx');
const QuickMenu = require('../../jsx/sidebar-menu/quick-menu.jsx');
const MenuDocsBurgerlijkWetboek = require('./menu-docs-burgerlijk-wetboek.jsx');
const MenuDocsOftenViewed = require('./menu-docs-often-viewed.jsx');
const MenuButton = require('../../jsx/sidebar-menu/menu-button.jsx');
const Overlay = require('../../jsx/sidebar-menu/menu-overlay.jsx');

const MenuActions = require('../../jsx/sidebar-menu/menu-actions');
const MenuStore = require('../../jsx/sidebar-menu/sidebar-menu-store');
const MenuConstants = require('../../jsx/sidebar-menu/menu-constants');

function getMenuState() {
    var _state = MenuStore.getState();
    return {isOpen: _state.isOpen};
}


const Home = React.createClass({
    mixins: [PureRenderMixin],

    propTypes: {},

    _onMenuStateChange: function () {
        //console.log("Change received: updating state: ");
        //console.log(getResultState());
        this.setState(getMenuState());
    },

    _closeMenu: function () {
        /*if (this.state.isOpen) */
        MenuActions.closeMenu();
    },


    _toggleMenu: function () {
        console.log("Changing menu state from {open:" +
            this.state.isOpen +
            "} to {open:" + !this.state.isOpen + "}");
        MenuActions.toggleMenu();
    },

    //componentDidUpdate: function (prevProps, prevState) {
    //    console.log("landing universe updated");
    //},

    getInitialState: function () {
        return getMenuState();
    },

    componentDidMount: function () {
        MenuStore.addChangeListener(this._onMenuStateChange);
    },

    componentWillUnmount: function () {
        MenuStore.removeChangeListener(this._onMenuStateChange);
    },

    render: function () {
        var openerStyle = {
            color: 'rgba(255, 255, 255, 0.7)',
            left: this.state.isOpen ? MenuConstants.menuWidth + 'px' : 0
        };
        //console.log("Render universe; menu button offset " + openerStyle.left);
        return <div id="the-universe">
            <Flag/>
            <div id="mount-point-menu-button">
                <MenuButton style={openerStyle}
                            isOpen={this.state.isOpen}
                            handleClick={this._toggleMenu}/>
            </div>
            <div id="outer" className="outer">
                <div className="middle">
                    <div className="inner">
                        <div id="lawly-splash-container">
                            <h1 id="lawly-splash">
                                <span id="lawly-splash-content">Wetten</span>
                            </h1>
                        </div>
                        <div id='landing-search-container'>
                            <form action='search/' id='landing-search-form' method='get'>
                                <input autofocus="autofocus" id='landing-search' maxLength='120' name='q'
                                       placeholder='Zoek een wettekst'
                                       type='text'/>
                                <input id="landing-search-button" type="submit" value=""/>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            <Overlay isOpen={this.state.isOpen} handleClick={this._closeMenu}/>
            <QuickMenu isOpen={this.state.isOpen}>
                <MenuDocsOftenViewed/>
                <MenuDocsBurgerlijkWetboek/>
            </QuickMenu>
        </div>;
    }
});

module.exports = Home;