const React = require('react');
const _ = require('underscore');
const ReactDOM = require('react-dom');
const PureRenderMixin = require('react-addons-pure-render-mixin');

const MenuConstants = require('./menu-constants');

const MenuButton = React.createClass({
    mixins: [PureRenderMixin],

    propTypes: {
        handleClick: React.PropTypes.func.isRequired,
        isOpen: React.PropTypes.bool.isRequired,
        style: React.PropTypes.object.isRequired
    },

    componentDidUpdate: function (prevProps, prevState) {
        console.log("menu button updated");
    },

    render: function () {
        var style = this.props.style;
        if (!style.left) {

        }
        if (!style.top) {
            style.top = 0;
        }
        if (!style.color) {
            style.color = '#333';
        }

        return <div style={style} id="menu-opener-container"
                    className={this.props.isOpen?"opened":''}>
            <button
                onClick={this.props.handleClick}
                id="menu-opener"><i
                style={{color: style.color}}
                className="fa fa-bars"> </i></button>
        </div>

    }
});

module.exports = MenuButton;