const https = require('https');

const lawlyBot = require('./lawly-bot');
const parseManifest = require('./parse-manifest');
const URL_BASE = lawlyBot.URL_LAWLY_BOT;

module.exports = {
    URL_BASE: URL_BASE,
    getManifest: function (bwbId) {
        return new Promise(function (resolve, reject) {
            //console.log("Fetching WTI for " + bwbId);

            const url = URL_BASE + bwbId + "/manifest.xml";

            console.log("Fetching " + url);

            const req = https.get(url, function (res) {
                parseManifest(res)
                    .then(resolve)
                    .catch(reject)
            });
            req.on('error', reject);
        });
    }
};