const React = require('react');
const PureRenderMixin = require('react-addons-pure-render-mixin');
const ReaderConstants = require('./reader-constants');

const NavBar = React.createClass({
    mixins: [PureRenderMixin],

    propTypes: {
        handleClick: React.PropTypes.func.isRequired
    },
    _yPos: 0,



    _onScroll: function () {
        var doc = document.documentElement;
        //var left = (window.pageXOffset || doc.scrollLeft) - (doc.clientLeft || 0);
        var top = (window.pageYOffset || doc.scrollTop) - (doc.clientTop || 0);

        if (this._yPos != top) {
            this._yPos = top;
            this.setState({yScrolled: top > 0});
        }
    },

    _toggleMenu: function () {
        console.log("TODO: open menu");
    },

    getDefaultProps: function () {
        return {}
    },

    getInitialState: function () {
        return {
            yScrolled: false
        };
    },

    componentDidMount: function () {
        window.addEventListener("scroll", this._onScroll);
    },


    componentWillUnmount: function () {
        window.removeEventListener("scroll", this._onScroll);
    },

    render: function () {
        var navIsOpen = this.props.isOpen;

        return <button
            style={{
              width: ReaderConstants.toggleBtnSize+'px',
              height: ReaderConstants.toggleBtnSize+'px',
              fontSize: '32px',
              lineHeight: ReaderConstants.toggleBtnSize+'px',
              textAlign: 'center',
              position: 'fixed',
              top: 0,
              border: 0,
              outline: 0,
              display: 'block'
            }}
            onClick={this.props.handleClick} id="nav-toggle"
            className={
            (navIsOpen?"nav-open":"nav-closed")
            +
            (this.state.yScrolled?" smooth-corner":"")}/>;
    }
});

module.exports = NavBar;