const React = require('react');
const _ = require('underscore');
const ReactDOM = require('react-dom');
const PureRenderMixin = require('react-addons-pure-render-mixin');

const Colors = require('material-ui/lib/styles/colors');
const RefreshIndicator = require('material-ui/lib/refresh-indicator');


const ToC = React.createClass({
    mixins: [PureRenderMixin],

    propTypes: {
        //status: React.PropTypes.isRequired
    },

    componentDidUpdate: function (prevProps, prevState) {
        console.log("Loading view updated");
    },

    render: function () {
        var size = 40;
        console.log("Render loading view");
        return <div style={{
        width: '100%',
        height: '100vh',
        display: 'table',
        position: 'fixed',
        top:0,bottom:0,left:0,right:0}}>
            <div style={{
            minHeight:  size+'px',
            width: '100%',
            display: 'table-cell',
            verticalAlign: 'middle'}}>
                <div style={{textAlign: 'center',width: '100%'}}>
                    <RefreshIndicator style={{
                    position: 'relative',
                    display:'inline-block'}} left={0} top={0} size={size}
                                      loadingColor={Colors.grey900}
                                      status="loading"/>
                </div>
            </div>
        </div>
    }
});

module.exports = ToC;