var sax = require("sax");

var strict = true;
var options = {};
/**
 *
 * @param inStream xml input stream
 */
function parseManifest(inStream) {
    return new Promise(function (resolve, reject) {
        var expressions = [];

        var currentExpression = null;
        var currentManifestation = null;
        var currentItem = null;
        var workLabel = null;

        var saxStream = sax.createStream(strict, options);


        //////////
        // End
        //////////
        saxStream.on("end", function () {
            resolve(expressions);
        });

        //////////
        // Error
        //////////
        saxStream.on("error", function (e) {
            console.error("error!", e);
            reject(e);
            //// clear the error
            //this._parser.error = null;
            //this._parser.resume()
        });


        //////////
        // Text
        //////////
        //saxStream.on("text", function (str) {
        //});


        ////////////////
        // Open element
        ////////////////
        saxStream.on("opentag", function (node) {
                const label = node.attributes['label'];

                if (node.name == 'work') {
                    workLabel = label;
                } else if (!!workLabel && node.name == 'expression') {
                    currentExpression = {
                        bwbId: workLabel,
                        label: label,
                        manifestations: {}
                    };
                } else if (!!workLabel && node.name == 'manifestation') {
                    currentManifestation = {
                        label: label,
                        item: null
                    };
                } else if (!!workLabel && node.name == 'item') {
                    currentItem = label;
                }
            }
        );

        saxStream.on("closetag", function (nodeName) {
            var node = {name: nodeName};
            if (node.name == 'expression') {
                expressions.push(currentExpression);
                currentExpression = null;
            } else if (node.name == 'manifestation') {
                currentExpression.manifestations[currentManifestation.label] = (currentManifestation);
                currentManifestation = null;
            } else if (node.name == 'item') {
                currentManifestation.item = currentItem;
                currentItem = null;
            }
        });

        // Pipe stream to parser
        inStream.pipe(saxStream);
    });
}

module.exports = parseManifest;