const React = require('react');
const PureRenderMixin = require('react-addons-pure-render-mixin');


var MenuConstants = require('./menu-constants');

const QuickMenu = React.createClass({
    mixins: [PureRenderMixin],

    getDefaultProps: function () {
        return {}
    },


    getInitialState: function () {
        return {};
    },

    render: function () {
        var menuStyle = {
            display: 'block',
            position: 'fixed',
            width: MenuConstants.menuWidth + 'px',
            overflow: 'auto',
            top: '0',
            left: this.props.isOpen ? 0 : ('-' + MenuConstants.menuWidth + 'px'),
            bottom: '0',
            zIndex: 10,
            background: 'transparent'
        };
        return (
            <div>
                <div style={menuStyle} id={"quick-menu"} className={this.props.isOpen?"opened":''}>
                    <div
                        style={{zIndex: 50}}
                        id='quick-menu-content-cntnr'>
                        <div id='quick-menu-content'>
                            {this.props.children}
                        </div>
                        <a style={{display: 'none'}} href="/all">.</a>
                        <footer id="footer">
                            <p>&copy; 2016 — Lawly</p>

                            <p id="contact_us">
                                <a href="mailto:info@lawly.nl">info@lawly.nl</a>
                            </p>
                        </footer>
                    </div>
                </div>
            </div>
        );
    }
});

module.exports = QuickMenu;