var EventEmitter = require('events').EventEmitter;
var assign = require('object-assign');
var https = require('https');
var React = require('react');
var _ = require('underscore');

var NavConstants = require('./reader-constants');
var AppDispatcher = require('../dispatcher/app-dispatcher');
const BwbUtil = require('./bwb/util');
const ToCEntry = require('./toc-entry.jsx');

const NAV_CHANGE_EVENT = 'nav_change';
const DOC_CHANGE_EVENT = 'doc_change';
const TOC_CHANGE_EVENT = 'toc_change';

var _createElementRecursive = function (root) {
    if (typeof root == 'string' || root.type) {
        // Is React component or string
        return root;
    } else {
        var _thisFormula = _createElementRecursive;
        var attrs = root.attrs;
        // TODO add key attribute
        return React.createElement(
            root.name,
            attrs,
            _.reduce(root.children, function (memo, element) {
                memo.push(_thisFormula(element));
                return memo;
            }, [])
        );
    }
};

function _createTocEntries(items) {
    return _.map(items, function (item, i) {
        const hasChildren = item.children &&
            item.children.length > 0;
        const children = hasChildren ?
            React.createElement("ol", {key: i + "-sublist"}, _createTocEntries(item.children))
            : [];
        return React.createElement(ToCEntry, {
            key: i,
            hasChildren: hasChildren,
            id: item.id,
            label: item.label,
            sublabel: item.sublabel,
            rangeStart: item.range && item.range.length > 0 ? item.range[0] : null,
            rangeEnd: item.range && item.range.length > 1 ? item.range[1] : null
        }, children);
    });
}

/**
 *
 * @param stack Given array
 * @returns {*} last element of given array
 */
function peek(stack) {
    if (stack.length > 0) {
        return stack[stack.length - 1];
    } else {
        throw new Error('Array was empty');
    }
}

/**
 * Stores the elements we encounter in order to make the ToC
 */
var _elementsFound = [];

var _state = {
    navigationIsOpen: true,
    /**
     * Parsed doc so far; when the doc is finished loading contains >= 0 React component
     */
    tocSoFar: [],

    docLoaded: false,

    /**
     * Parsed doc so far; when the doc is finished loading contains a single React component
     */
    docSoFar: []
};

var ReaderStore = assign({}, EventEmitter.prototype, {
    getTocEntriesSoFar: function () {
        return _state.tocSoFar;
    },

    getDocSoFar: function () {
        if (_state.docSoFar.length > 0) {
            var soFar = _state.docSoFar;
            //var rootNode = soFar[0];
            //for (var l = soFar.length - 1; l > 0; l--) {
            //    var latestChild = {
            //        name: soFar[l].name,
            //        xml: soFar[l].xml,
            //        attrs: soFar[l].attrs,
            //        children: []
            //    };
            //    rootNode = {
            //        name: soFar[l - 1].name,
            //        xml: soFar[l - 1].xml,
            //        attrs: soFar[l - 1].attrs,
            //        children: []
            //    };
            //    rootNode.children.push(latestChild);
            //}
            //_state.tocSoFar = BwbUtil.generateToc(rootNode);

            var copiez = [];
            for (var i = 0; i < soFar.length; i++) {
                if (typeof soFar[i] == 'string' || soFar[i].type) {
                    copiez.push(soFar[i]);
                } else {
                    // Make a defensive copy
                    var copii = {
                        name: soFar[i].name,
                        xml: soFar[i].xml,
                        attrs: soFar[i].attrs,
                        children: []
                    };
                    // Copy children
                    for (var j = 0; j < soFar[i].children.length; j++) {
                        //console.log("Copy :"+soFar[i].children[j]);
                        copii.children.push(soFar[i].children[j]);
                    }
                    copiez.push(copii);
                }
            }

            for (var n = copiez.length - 1; n > 0; n--) {
                copiez[n - 1].children.push(copiez[n]);
            }

            //console.log("Copiez");
            //console.log(copiez);

            return _createElementRecursive(copiez[0]);
        } else {
            return '';
        }
    },
    /**
     * Get the entire collection of results.
     */
    getState: function () {
        return _state;
    },

    emitNavChange: function () {
        this.emit(NAV_CHANGE_EVENT);
    },
    emitDocChange: function () {
        this.emit(DOC_CHANGE_EVENT);
        this.emit(TOC_CHANGE_EVENT);
    },
    //emitTocChange: function () {
    //},

    /**
     * @param {function} callback
     */
    addNavMenuChangeListener: function (callback) {
        this.on(NAV_CHANGE_EVENT, callback);
    },
    addDocChangeListener: function (callback) {
        this.on(DOC_CHANGE_EVENT, callback);
    },
    addTocChangeListener: function (callback) {
        this.on(TOC_CHANGE_EVENT, callback);
    },

    /**
     * @param {function} callback
     */
    removeNavChangeListener: function (callback) {
        this.removeListener(NAV_CHANGE_EVENT, callback);
    },
    removeDocChangeListener: function (callback) {
        this.removeListener(DOC_CHANGE_EVENT, callback);
    },
    removeTocChangeListener: function (callback) {
        this.removeListener(TOC_CHANGE_EVENT, callback);
    }
});

// Register callback to handle all updates
AppDispatcher.register(function (action) {
    switch (action.actionType) {
        case NavConstants.NAV_CLOSE:
            _state.navigationIsOpen = false;
            ReaderStore.emitNavChange();
            break;
        case NavConstants.NAV_TOGGLE:
            //console.log(_state.navigationIsOpen +" = "+!_state.navigationIsOpen);
            _state.navigationIsOpen = !_state.navigationIsOpen;
            ReaderStore.emitNavChange();
            break;
        case NavConstants.DOC_PARSE_MSG:
            var e = action.message;
            switch (e.data[0]) {
                case 'end':
                    //console.log("Done parsing XML");
                    //console.log();
                    _state.docLoaded = true;
                    ReaderStore.emitDocChange();
                    break;
                case 'opentag':
                    var parsed = e.data[1];
                    //console.log("Open: " + parsed.xml);

                    if (_state.docSoFar.push) {
                        _state.docSoFar.push(parsed);
                        _elementsFound.push({
                            name: parsed.name,
                            xml: parsed.xml,
                            attrs: parsed.attrs,
                            children: []
                        });
                    } else {
                        console.error("Could not add " + parsed.xml);
                    }
                    break;
                case 'closetag':
                    var nodeName = e.data[1];
                    //console.log("Close: " + nodeName + ", elements in stack: " + result.length);
                    if (_state.docSoFar.length && _state.docSoFar.length > 0) {
                        var element = _state.docSoFar.pop();
                        var elForToc = _elementsFound.pop();
                        if (nodeName != element.xml) {
                            console.error("Expected " + element.xml + " instead of " + nodeName);
                        }
                        if (_state.docSoFar.length > 0) {
                            var el = peek(_state.docSoFar);
                            //console.log("Appending " + element.xml + " to " + el.xml);
                            var reactComponent = _createElementRecursive(element);
                            el.children.push(reactComponent);

                            peek(_elementsFound).children.push(elForToc);

                            // Only do intermittent chang emits for parsed articles
                            if (element.name == 'article') {
                                ReaderStore.emitDocChange();
                            }
                        } else {
                            //console.log("done: ");
                            //console.log(element);
                            _state.docSoFar = [element];
                            _state.tocSoFar = _createTocEntries(BwbUtil.generateToc(elForToc));
                            ReaderStore.emitDocChange();
                        }
                    } else {
                        console.error("No tags were open.");
                    }
                    break;
                case 'text':
                    var str = e.data[1];
                    // Replace multiple whitespace characters by a single space
                    if (_state.docSoFar.length && _state.docSoFar.length > 0) {
                        var elementChildren = peek(_state.docSoFar).children;
                        if (!(elementChildren.length > 0 &&
                            elementChildren[elementChildren.length - 1] == ' ')) {
                            elementChildren.push(str);
                        }

                        var _elementChildren = peek(_elementsFound).children;
                        if (!(_elementChildren.length > 0 &&
                            _elementChildren[_elementChildren.length - 1] == ' ')) {
                            _elementChildren.push(str);
                        }
                    }
                    break;
                default:
                // no op
            }
    }
});

module.exports = ReaderStore;
