var AppDispatcher = require('../dispatcher/app-dispatcher');
var SearchConstants = require('./search-constants');

var SearchActions = {
    newSearch: function (query) {
        AppDispatcher.dispatch({
            actionType: SearchConstants.SEARCH_NEW,
            query: query
        });
    },
    nextPage: function () {
        AppDispatcher.dispatch({
            actionType: SearchConstants.SEARCH_NEXT_PAGE
        });
    },
    destroy: function () {
        AppDispatcher.dispatch({
            actionType: SearchConstants.SEARCH_DESTROY
        });
    }
};

module.exports = SearchActions;
