var React = require('react');
var ReactDOM = require('react-dom');
var ReaderMain = require('./reader/reader-main.jsx');
var exprs = require('./reader/backend/expressions');
var works = require('./reader/backend/works');
var LoadingView = require('./reader/loading.jsx');

function getUrlParams() {
    var match,
        search = /([^&=]+)=?([^&]*)/g,
        decode = function (s) {
            return decodeURIComponent(s.replace(/\s+/g, ""));
        },
        query = window.location.search.substring(1);

    var urlParams = {};
    while (match = search.exec(query))
        urlParams[decode(match[1])] = decode(match[2]);
    return urlParams;
}
var urlParams = getUrlParams();

var readerNode = document.getElementById('mount-point-reader');
if (readerNode) {
    if (urlParams.bwbId) {
        if (urlParams.version) {
            ReactDOM.render(<ReaderMain bwbId={urlParams.bwbId} version={urlParams.version}/>, readerNode);
        } else {
            ReactDOM.render(<LoadingView/>, readerNode);

            console.log("Getting manifest for " + urlParams.bwbId);
            works.getManifest(urlParams.bwbId)
                .then(exprs.getSingleExpression)
                .then(function (expr) {

                    var bwbId = urlParams.bwbId;
                    var label = expr.label;
                    if (typeof bwbId != 'string' || typeof label != 'string') throw new Error();
                    ReactDOM.render(<ReaderMain
                            bwbId={bwbId}
                            version={label}/>,
                        readerNode);
                })
                .catch(function (err) {
                    //TODO
                    console.error(err);
                })

        }
    } else {
        //TODO
        //ReactDOM.render(<ParamsNotSet/>, mountNode);
        console.error("bwbId and version parameters should be set.");
    }
//} else {
//    console.log(" reader node not found");
}

var searchNode = document.getElementById('mount-point-search');
if (searchNode) {
    var Search = require('../jsx/search/search.jsx');
    ReactDOM.render(<Search query={urlParams.q}/>,
        searchNode);
}

var homeNode = document.getElementById('mount-point-home');
if (homeNode) {
    var Home = require('../html/memes/main-content.jsx');
    ReactDOM.render(<Home/>, homeNode);
}

var loadingNode = document.getElementById('mount-point-loading');
if (loadingNode) {
    ReactDOM.render(<LoadingView/>, loadingNode);
}


