const https = require('https');

module.exports =
    function (url) {
        return new Promise(function (resolve, reject) {
            //console.log("DLing " + url);
            https.get(url, function (res) {
                //console.log("Response gotten for " + url);
                if (res.statusCode != 200 && res.statusCode != 340) {
                    //console.error("Server responded with " + res.statusCode);
                    reject(res);
                    return;
                }

                var pageData = [];
                res.setEncoding('utf-8');
                res.on('data', function (chunk) {
                    pageData.push(chunk);
                });

                res.on('end', function () {
                    resolve(pageData.join());
                });
            }).on('error', function (e) {
                reject(e);
            });
        });
    };