const React = require('react');
const PureRenderMixin = require('react-addons-pure-render-mixin');

const MenuConstants = require('../sidebar-menu/menu-constants');
const ReaderConstants = require('./reader-constants');
//const MenuButton = require('../sidebar-menu/menu-button.jsx');
//const NavigationToggle = require('./navigation-toggle.jsx');

const NavBar = React.createClass({
    mixins: [PureRenderMixin],

    propTypes: {
        title: React.PropTypes.string.isRequired,
        navigationIsOpen: React.PropTypes.bool.isRequired,
        menuIsOpen: React.PropTypes.bool.isRequired
    },

    getDefaultProps: function () {
        return {
            title: "Wetten"
        }
    },

    getInitialState: function () {
        return {};
    },

    render: function () {
        //console.log("render navbar");

        return <nav id="top-nav-bar">
            <div id="left-container"
                     className={this.props.navigationIsOpen?"nav-open":'nav-closed'}
                 style={{
                             left:
                               this.props.navigationIsOpen?
                                 ReaderConstants.navWidth+"px"
                                 :0
                                                 //marginLeft: (28+'px')
                                             }}
            >
                <div id="title-container"
                ><a href="../"
                    id="title-anchor"
                >Wetten</a>

                    <div id="arrow-container">
                        <div id="arrow"></div>
                    </div>
                </div>
            </div>

        </nav>;
    }
    //<div id="search-container">
    //    <form action="/search" id="search-form" method="get">
    //        <input id="search" maxLength="120" name="q" placeholder="Zoek een wettekst" type="text"/>
    //    </form>
    //</div>
});

module.exports = NavBar;