const React = require('react');
const _ = require('underscore');
const PureRenderMixin = require('react-addons-pure-render-mixin');

//const ReaderActions = require('./reader-actions');
const ReaderStore = require('./reader-store');

const ReaderConstants = require('./reader-constants');


function getTocState() {
    return {};
}
const ToC = React.createClass({
    _onTocChanged: function () {
        this.setState(getTocState());
    },

    mixins: [PureRenderMixin],

    propTypes: {
        //toc: React.PropTypes.array.isRequired,
        isOpen: React.PropTypes.bool.isRequired
    },

    componentDidMount: function (prevProps, prevState) {
        ReaderStore.addTocChangeListener(this._onTocChanged);
    },
    componentWillUnmount: function () {
        ReaderStore.removeTocChangeListener(this._onTocChanged);
    },
    componentDidUpdate: function (prevProps, prevState) {
        console.log("ToC updated");
    },
    getInitialState(){
        return getTocState();
    },
    render: function () {
        var items = this.state.tocSoFar;
        //console.log(items);
        var navStyle = {
            padding: '0',
            margin: '0',
            backgroundColor: '#fff',
            width: ReaderConstants.navWidth + 'px',
            position: 'fixed',
            top: '0',
            left: this.props.isOpen ? 0 : "-" + ReaderConstants.navWidth + "px",
            bottom: '0',
            zIndex: '2000',
            overflowY: 'auto'
        };

        return <div id="drawer" className={this.props.isOpen?"nav-shown":"nav-hidden"}>
            <div style={navStyle} id="navigation-menu">
                <nav id="document-navigation">
                    <ol>
                        {this.props.children}
                    </ol>
                </nav>
            </div>
        </div>
    }
});

module.exports = ToC;