var EventEmitter = require('events').EventEmitter;
var assign = require('object-assign');
var https = require('https');

var MenuConstants = require('./menu-constants');
var AppDispatcher = require('../dispatcher/app-dispatcher');

const MENU_CHANGE_EVENT = 'menu_change';

var _state = {
    isOpen: false
};

var SidebarMenuStore = assign({}, EventEmitter.prototype, {

    /**
     * Get the entire collection of results.
     */
    getState: function () {
        return _state;
    },

    emitMenuChange: function () {
        this.emit(MENU_CHANGE_EVENT);
    },

    /**
     * @param {function} callback
     */
    addChangeListener: function (callback) {
        this.on(MENU_CHANGE_EVENT, callback);
    },

    /**
     * @param {function} callback
     */
    removeChangeListener: function (callback) {
        this.removeListener(MENU_CHANGE_EVENT, callback);
    }
});

// Register callback to handle all updates
AppDispatcher.register(function (action) {
    switch (action.actionType) {
        case MenuConstants.MENU_CLOSE:
            _state.isOpen = false;
            SidebarMenuStore.emitMenuChange();
            break;
        case MenuConstants.MENU_TOGGLE:
            _state.isOpen = !_state.isOpen;
            SidebarMenuStore.emitMenuChange();
            break;
        default:
        // no op
    }
});

module.exports = SidebarMenuStore;
