const sax = require("sax");
const escapeHtml = require('escape-html');

var strict = true;
var options = {};
const _WHITESPACE = ' ';

/**
 *
 * @param stack Given array
 * @returns {*} last element of given array
 */
function peek(stack) {
    return stack.length > 0 ? stack[stack.length - 1] : null;
}

var ignoredElements = {
    'alias-titels': true,
    'meta-data': true
};
var htmlMappings = {
    al: 'p',
    lijst: 'ul',
    nadruk: 'em',
    intitule: 'h1',
    titel: 'h3',
    title: 'h3',
    tussenkop: 'h3',
    label: 'h3',
    nr: 'h3',
    lidnr: 'h4',
    artikel: 'article',
    dagtekening: 'div',
    plaats: 'span',
    datum: 'span',
    functie: 'span',
    redactie: 'span',
    naam: 'span',
    voornaam: 'span',
    achternaam: 'span',
    organisatie: 'span',
    sup: 'sup',
    sub: 'sub',
    intref: 'a', // TODO handle intrefgroep
    extref: 'a', // TODO handle extrefgroep
    illustratie: 'img', // TODO properly handle get src url
    table: 'div', // TODO handle the myriad table attrs
    colspec: 'col',
    thead: 'thead',
    tfoot: 'tfoot',
    entrytbl: 'table',
    tbody: 'tbody',
    li: 'li',
    kop: 'header',
    row: 'tr'
};

/**
 *
 * @param name tagname string
 * @param xmlTag xml tag name string
 * @param attrs string to string map
 * @param children Array of strings
 * @returns {{name: *, attrs: *, children: *}}
 */
function createElement(name, xmlTag, attrs, children) {
    return {
        name: name,
        xml: xmlTag,
        attrs: attrs,
        children: children
    };
}
function setAttr(xmlKey, xmlAttrs, htmlKey, htmlAttrs) {
    htmlAttrs[htmlKey] = escapeHtml(xmlAttrs[xmlKey]);
}
function getHtmlAttributes(node) {
    var attrs = {
        'class': node.name
        //'data-xml-tag': xmlTag
    };
    var nas = node.attributes;
    if (nas['bwb-ng-variabel-deel'] && !(node.name.match(/(int?|ext)ref/))) {
        setAttr('bwb-ng-variabel-deel', nas, 'id', attrs);
    } else if (nas['if']) {
        setAttr('id', nas, 'id', attrs);
    }

    for (var key in nas) {
        if (nas.hasOwnProperty(key))
            if (key == 'id') {
                setAttr(key, nas, 'data-id', attrs);
            } else if (key == 'stam-id') {
                setAttr(key, nas, 'data-stam-id', attrs);
            } else if (key == 'versie-id') {
                setAttr(key, nas, 'data-versie-id', attrs);
            } else if (key == 'label-id') {
                setAttr(key, nas, 'data-label-id', attrs);
                //doc="32004L0017" label="richtlijn" reeks="Celex"
                //verwijzing-id="2556878" doc="" bwb-id="" label-id="12115324"
            } else if (key == 'bwb-id') {
                setAttr(key, nas, 'data-bwb-id', attrs);
            } else if (key == 'verwijzing-id') {
                setAttr(key, nas, 'data-verwijzing-id', attrs);
            } else if (key == 'label') {
                setAttr(key, nas, 'data-label', attrs);
            } else if (key == 'reeks') {
                setAttr(key, nas, 'data-reeks', attrs);
            } else if (key == 'doc') {
                setAttr(key, nas, 'data-doc', attrs);

            } else {
                //console.log(attr);
            }
    }
    return attrs;
}

function simpleNodeHtml(htmlTag, node) {
    return createElement(htmlTag, node.name, getHtmlAttributes(node), []);
}


function getAttributesAsString(attrs) {
    if (attrs.length <= 0) {
        return ''
    } else {
        var ss = [];
        for (var key in attrs) {
            if (attrs.hasOwnProperty(key))
                ss.push('' + key + '="' + attrs[key] + '"');
        }
        return ' ' + ss.join(' ');
    }
}

function setAnchor(xmlNode, htmlNode) {
    // TODO parse version (jcil); CELEX, etc.
    var nas = xmlNode.attributes;
    if (nas['bwb-id']) {
        htmlNode.attrs['href'] = '../reader/?bwbId=' + nas['bwb-id'];
    }
}

/**
 *
 * @param inStream xml input stream
 * @param cb callback function, receives HTML string
 */
function toHtml(inStream, cb) {
    var elementStack = [];
    var html = null;
    var rootNode = createElement("article", "wetgeving", {
        'class': "wetgeving legal-document wrapper"
    }, []);

    function currentElHasIgnoredParent() {
        if (peek(elementStack)) {
            return peek(elementStack).children == undefined;
        }
        return false;
    }

    function shouldIgnore(node) {
        return !!(!!ignoredElements[node.name] || !!currentElHasIgnoredParent());
    }

    function xmlNodeToHtmlNode(node) {
        var nm = node.name;

        if ('entry' == nm) {
            if (peek(elementStack) && peek(elementStack).xml == 'thead')
                return simpleNodeHtml('th', node);
            if (peek(elementStack) && peek(elementStack).xml == 'tbody')
                return simpleNodeHtml('td', node);
            if (peek(elementStack) && peek(elementStack).xml == 'row')
                return simpleNodeHtml('td', node);
        }


        // Check if there is a simple mapping
        for (var tagName in htmlMappings) {
            if (tagName == nm && htmlMappings.hasOwnProperty(tagName)) {
                var htmlNod = simpleNodeHtml(htmlMappings[tagName], node);
                if (tagName == 'extref') {
                    setAnchor(node, htmlNod);
                }
                return htmlNod;
            }
        }

        if ('tgroup' == nm) {
            // TODO <tgroup align="left" char="" charoff="50" cols="3" colsep="0" rowsep="0">
            return simpleNodeHtml('table', node);
        }

        if('meta' == nm){
            // TODO handle meta tag
        }

        // Default: just make a <div>
        return simpleNodeHtml('div', node);
    }

    var saxStream = sax.createStream(strict, options);
    saxStream.on("end", function () {
        cb(html);
    });
    saxStream.on("error", function (e) {
        // unhandled errors will throw, since this is a proper node
        // event emitter.
        console.error("error!", e);

        // clear the error
        this._parser.error = null;
        this._parser.resume()
    });

    saxStream.on("text", function (str) {
        if (peek(elementStack) && !currentElHasIgnoredParent()) {
            if (str.match(/^\s+$/)) {
                // Replace multiple whitespace characters by a single space
                peek(elementStack).children.push(_WHITESPACE);
            } else {
                peek(elementStack).children.push(escapeHtml(str));
            }
        }
    });

    saxStream.on("opentag", function (node) {
            if (elementStack.length <= 0 && node.name != 'wetgeving') return; // We haven't even started yet

            var ignore = shouldIgnore(node);
            var htmlNode = {xml: node.name};
            if (ignore) {
            } else {
                if (node.name == 'wetgeving') {
                    /**
                     * Root node
                     */
                        //console.log('rootnode set: ' + rootNode);
                    htmlNode = rootNode;
                }

                if (elementStack.length > 0) {
                    htmlNode = xmlNodeToHtmlNode(node);
                }
            }
            elementStack.push(htmlNode);
        }
    );

    saxStream.on("closetag", function (nodeName) {
        if (elementStack.length <= 0) return; // We're already done

        var element = elementStack.pop();
        if (nodeName != element.xml) {
            console.error("Expected " + element.xml + " instead of " + nodeName);
        }
        if (element.attrs) {
            var elementString =
                "<" + element.name + getAttributesAsString(element.attrs) + ">" +
                element.children.join('') +
                "</" + element.name + ">";

            var parentNode = peek(elementStack);
            if (parentNode) {
                parentNode.children.push(elementString);
            } else {
                html = elementString;
            }
        }
    });

    // Pipe stream to parser
    inStream.pipe(saxStream);
}

module.exports = toHtml;