const https = require('https');
const _ = require('underscore');

const toHtml = require('./xml-to-html');
const works = require('./works');

const URL_BASE = works.URL_BASE;

const getHtmlFromStream = function (stream) {
    return new Promise(function (resolve, reject) {
        toHtml(stream, function (html, err) {
                if (err) {
                    reject(err);
                    return;
                }
                resolve(html);
            }
        );
    });
};

const getSingleExpression = function (expressions, versionId) {
    return new Promise(function (resolve, reject) {
        //console.log("Expressions: ", expressions);
        if (versionId) {
            expressions = _.filter(expressions, function (manifestation) {
                return manifestation.label == versionId;
            });
        } else {
            // Sort
            expressions = _.sortBy(expressions, function (e) {
                return e.label;
            });
        }

        if (expressions.length > 0) {
            resolve(expressions[expressions.length - 1]);
        } else {
            reject(Error("Found " + expressions.length + " expressions."));
        }
    });
};

const getHtmlStringFromBwbId = function (bwbId, versionId) {
    return new Promise(function (resolve, reject) {
            works.getManifest(bwbId)
                .then(function (exprs) {
                    return getSingleExpression(exprs, versionId)
                })
                .then(getHtmlAsString)
                .then(resolve)
                .catch(reject);
        }
    );
};

const getHtmlAsString = function (expression) {
    return new Promise(function (resolve, reject) {
        const url = URL_BASE + "" + expression.bwbId + "/" + expression.label +
            "/xml/" + expression.manifestations.xml.item;
        //console.log("Fetching " + url);
        https.get(url, function (stream) {
            toHtml(stream, function (html, err) {
                    if (err) {
                        reject(err);
                        return;
                    }
                    resolve(html);
                }
            );
        });
    });
};

module.exports = {
    getHtmlAsStringFromBwbId: getHtmlStringFromBwbId,
    getHtmlAsString: getHtmlAsString,
    getHtmlFromStream: getHtmlFromStream,
    getSingleExpression: getSingleExpression
};