var React = require('react');
var _ = require('underscore');
var ReactDOM = require('react-dom');
const PureRenderMixin = require('react-addons-pure-render-mixin');
const TextField = require('material-ui/lib/text-field');
const Colors = require('material-ui/lib/styles/colors');

var SearchActions = require('./search-actions');
var ResultsStore = require('./results-store');
var SearchResult = require('./search-result.jsx');

/**
 * Retrieve the current data from the ResultsStore
 */
function getResultState() {
    return ResultsStore.getState();
}

const HelloMessage = React.createClass({
    mixins: [PureRenderMixin],

    propTypes: {
        query: React.PropTypes.string
    },

    getInitialState: function () {
        return {};
    },

    componentDidMount: function () {
        ResultsStore.addChangeListener(this._onResultsChange);
        if (!this.state.query && this.props.query) {
            // Initial run:
            this._handleQueryChange({target: {value: this.props.query}});
        }
    },

    componentWillUnmount: function () {
        ResultsStore.removeChangeListener(this._onResultsChange);
    },

    /**
     * Event handler for 'change' events coming from the ResultsStore
     */
    _onResultsChange: function () {
        //console.log("Change received: updating state: ");
        //console.log(getResultState());
        this.setState(getResultState());
    },

    _makeResponse: function () {
        if (this.state.error) {
            return <div id="search-error">Er ging iets mis.</div>;
        } else if (this.state.loading) {
            //TODO loading view
        } else if (this.state.results) {
            if (this.state.results.length < 1) {
                return <div style={{textAlign: 'center'}} id="no-results">0 resultaten.</div>;
            } else {
                var results = _.map(this.state.results, function (result, i) {
                    return <SearchResult
                        id={result.id}
                        type={result.fields.kind}
                        title={result.fields.title}
                        key={i}/>
                });
                return <ul id="search-results">
                    {results}
                </ul>;
            }
        }
    },

    getDefaultProps: function () {
        return {
            limit: 20
        };
    },

    promise: null,
    _handleQueryChange: function (event) {
        var query = event.target.value;

        //console.log("Query changed: " + event.target.value);
        this.setState({
            query: query
        });

        if (query.trim() == '') {
            SearchActions.destroy();
        } else {
            if (this.promise) {
                clearTimeout(this.promise);
            }
            this.promise = setTimeout(function () {
                console.log("Starting action of searching " + query);
                SearchActions.newSearch(query);
            }.bind(this), 200);
        }
    },

    render: function () {
        return <div>
            <div
                style={{borderBottom: '#ccc solid 1px',padding: '0 24px'}}
            >
                <TextField
                    id='search'
                    onChange={this._handleQueryChange}
                    value={this.state.query}
                    fullWidth={true}
                    autofocus="autofocus"
                    floatingLabelStyle={{color: Colors.grey900}}
                    underlineFocusStyle={{borderColor:Colors.grey900}}
                    floatingLabelText="Zoek een tekst"/>
            </div>
            {this._makeResponse()}
        </div>;
    }
});

module.exports = HelloMessage;