var EventEmitter = require('events').EventEmitter;
var assign = require('object-assign');
var https = require('https');

var TodoConstants = require('./search-constants');
var AppDispatcher = require('../dispatcher/app-dispatcher');

const CHANGE_EVENT = 'change';
const ERROR_EVENT = 'error';
const CANCELLED = "Cancelled";

var _limit = 20;

var _lastToken = {};
function resetResults() {
    _lastToken = {
        results: [],
        error: null,
        total: null,
        bookmark: null,
        lastUrl: null,
        cancel: function () {
        }
    };
}

resetResults();

function makeUrl(query, limit, skip) {
    var url = "https://lawly.cloudant.com/bwb/_design/RegelingInfo/_search/" +
        "nonEmptyDocs?" +
        "limit=" + limit +
        "&skip=" + skip +
        "&q=" + encodeURIComponent("default:" + query + "* OR default:" + query);
    return url;
}

function performCancelableRequest(url) {
    return new Promise(function (resolve, reject) {
        var requestDone = false;
        var req = https.get(url, function (res) {
            //console.log("Response gotten for " + url);
            if (res.statusCode != 200 && res.statusCode != 340) {
                //console.error("Server responded with " + res.statusCode);
                reject(new Error(res));
                return;
            }

            var pageData = [];
            res.setEncoding('utf-8');
            res.on('data', function (chunk) {
                pageData.push(chunk);
            });

            res.on('abort', function () {
                console.log("Request aborted for " + url);
            });
            res.on('end', function () {
                requestDone = true;
                resolve(pageData.join());
            });
        }).on('error', function (e) {
            reject(e);
        });

        _lastToken.cancel = function () {
            req.abort(); // abort request

            reject(new Error(CANCELLED)); // reject the promise
        };
    });
}
function performSearch(url) {
    _lastToken.cancel(); // Cancel last request

    return performCancelableRequest(url)
        .then(function (str) {
            _lastToken.cancel = function () {
                reject(new Error(CANCELLED)); // reject the promise
            };

            var json = JSON.parse(str);
            var results = json.rows;
            _lastToken.bookmark = json.bookmark;
            _lastToken.total = json.total_rows;
            _lastToken.results = _lastToken.results.concat(results);

            return _lastToken;
        });
}

/**
 * Redoes search.
 * @param  {string} query The search query
 */
function newSearch(query) {
    resetResults();

    //_lastToken.query = query;

    // Figure out URL
    var url = makeUrl(query, _limit, 0);
    _lastToken.lastUrl = url;
    console.log("performing new search query: " + query + ", " + url);

    // Perform query
    return performSearch(url);
}

function nextPage() {
    if (_lastToken.bookmark && _lastToken.lastUrl) {
        var url = _lastToken.lastUrl + "&bookmark=" + _lastToken.bookmark;
        return performSearch(url);
    } else {
        throw new Error("Illegal state: no search request performed yet (i.e., no bookmark/lastUrl found)");
    }
}


function destroyResults() {
    _lastToken = {
        results: null,
        error: null,
        total: null,
        bookmark: null,
        lastUrl: null,
        cancel: function () {
        }
    };
}

var ResultsStore = assign({}, EventEmitter.prototype, {

    /**
     * Get the entire collection of results.
     */
    getState: function () {
        return _lastToken;
    },

    emitChange: function () {
        this.emit(CHANGE_EVENT);
    },

    emitError: function (e) {
        if (e.message == CANCELLED) {
            console.log("Cancelled - OK");
        } else {
            this.emit(ERROR_EVENT);
        }
    },

    /**
     * @param {function} callback
     */
    addChangeListener: function (callback) {
        this.on(CHANGE_EVENT, callback);
    },

    /**
     * @param {function} callback
     */
    removeChangeListener: function (callback) {
        this.removeListener(CHANGE_EVENT, callback);
    }
});

// Register callback to handle all updates
AppDispatcher.register(function (action) {
    var normalizedQuery;

    switch (action.actionType) {
        case TodoConstants.SEARCH_NEW:
            normalizedQuery = action.query.trim();
            if (normalizedQuery !== '') {
                newSearch(normalizedQuery)
                    .then(function () {
                        console.log("Performed search query: " + normalizedQuery);
                        ResultsStore.emitChange();
                    })
                    .catch(function (e) {
                        console.error(e);
                        ResultsStore.emitError(e);
                    });
                //} else {
                //    resetResults();
                //    ResultsStore.emitChange();
            }
            break;
        case TodoConstants.SEARCH_DESTROY:
            destroyResults();
            ResultsStore.emitChange();
            break;
        case TodoConstants.SEARCH_NEXT_PAGE:
            if (_lastToken.bookmark && _lastToken.lastUrl) {
                nextPage()
                    .then(function () {
                        ResultsStore.emitChange();
                    })
                    .catch(function (e) {
                        console.error(e);
                        ResultsStore.emitError(e);
                    });
            }
            break;

        default:
        // no op
    }
});

module.exports = ResultsStore;
