var AppDispatcher = require('../dispatcher/app-dispatcher');
var ReaderConstants = require('./reader-constants');

var NavActions = {
    close: function () {
        AppDispatcher.dispatch({
            actionType: ReaderConstants.NAV_CLOSE
        });
    },
    toggle: function () {
        AppDispatcher.dispatch({
            actionType: ReaderConstants.NAV_TOGGLE
        });
        //console.log("NavToggle action dispatched");
    },
    getHtml: function (bwbId) {
        AppDispatcher.dispatch({
            actionType: ReaderConstants.LOAD_HTML,
            bwbId: bwbId
        });
    },
    notifyDocParseUpdate: function (msg) {
        AppDispatcher.dispatch({
            actionType: ReaderConstants.DOC_PARSE_MSG,
            message: msg
        });
    }
};

module.exports = NavActions;
